import React, { useState } from "react";
import { styled, alpha } from "@mui/material/styles";
import InputBase from "@mui/material/InputBase";
import SearchIcon from "@mui/icons-material/Search";
import PlaceIcon from "@mui/icons-material/Place";
import Paper from "@mui/material/Paper";
import Stack from "@mui/material/Stack";
import IconButton from "@mui/material/IconButton";
import AddShoppingCartIcon from "@mui/icons-material/AddShoppingCart";
import RemoveIcon from "@mui/icons-material/Remove";
import Slider from "@mui/material/Slider";
import Box from "@mui/material/Box";

import css from "./SearchBar.module.css";


const Item = (items) => <Paper className={css.item}>{items.children}</Paper>
const SmallItem = (items) => <Paper className={css.smallItem}>{items.children}</Paper>
const Search = (items) => <div className={css.search}>{items.children}</div>


// const Search = styled("div")(({ theme }) => ({
//   position: "relative",
//   borderRadius: theme.shape.borderRadius,
//   backgroundColor: alpha(theme.palette.common.white, 0.15),
//   "&:hover": {
//     backgroundColor: alpha(theme.palette.common.white, 0.25),
//   },
//   marginRight: theme.spacing(2),
//   marginLeft: 0,
//   width: "100%",
//   [theme.breakpoints.up("sm")]: {
//     width: "auto",
//   },
// }));

const SearchIconWrapper = (icon) => (
  <div className={css.searchIconWrapper}>{icon.children}</div>
);


const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: "inherit",
  "& .MuiInputBase-input": {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create("width"),
    width: "100%"
  },
}));

export default function SearchBar(props) {
  const [isShown, setIsShown] = useState(false);
  const [value, setValue] = React.useState(new Date('2018-01-01T00:00:00.000Z'));

  // const isBigScreen = window.matchMedia("(min-width: 800px)").matches;
  return (
    <>
      <Stack
        className={css.stack}
        direction={{ md: "column", lg: "row" }}
        spacing={2}
        width="100%"
        style={{ margin: "10px" }}
      >
        <Item>
          <Search>
            <SearchIconWrapper>
              <SearchIcon />
            </SearchIconWrapper>
            <StyledInputBase
              value={props.data.name}
              onChange={(event) => {
                props.updateData({
                  ...props.data,
                  ...{ name: event.target.value },
                });
              }}
              placeholder="Czego potrzebujesz?"
              inputProps={{ "aria-label": "search" }}
            />
          </Search>
        </Item>
        <Item>
          <Search>
            <SearchIconWrapper>
              <PlaceIcon />
            </SearchIconWrapper>
            <StyledInputBase
              value={props.data.localization}
              placeholder="Gdzie?"
              onChange={(event) => {
                props.updateData({
                  ...props.data,
                  ...{ localization: event.target.value },
                });
              }}
              inputProps={{ "aria-label": "where" }}
            />
          </Search>
        </Item>
        <Item>
          <Search>
            <label>Od: </label>
            <StyledInputBase
            className={css.dateTimePicker}
              value={props.data.from}
              onChange={(event) => {
                props.updateData({
                  ...props.data,
                  ...{ from: event.target.value },
                });
              }}
              inputProps={{ "aria-label": "from", type: "datetime-local"}}
            />
          </Search>
        </Item>
        <Item>
          <Search>
            <label>Do: </label>
            <StyledInputBase
              value={props.data.to}
              onChange={(event) => {
                props.updateData({
                  ...props.data,
                  ...{ to: event.target.value },
                });
              }}
              inputProps={{ "aria-label": "to", type: "datetime-local", width: "100%" }}
            />
          </Search>
        </Item>
        {/* <Item>
          <Box
            width={300}
            style={{ padding: "0px 10px 0px 10px" }}
            onMouseEnter={() => setIsShown(true)}
            onMouseLeave={() => setIsShown(false)}
          >
            {isShown ? (
              <Slider
                valueLabelFormat={(value) => `budet: ${value} z`}
                min={40}
                max={210}
                defaultValue={50}
                aria-label="Default"
                valueLabelDisplay="auto"
                value={props.data.price}
                onChange={(event) => {
                  props.updateData({
                    ...props.data,
                    ...{ price: event.target.value },
                  });
                }}
              />
            ) : (
              <StyledInputBase value={`budet: ${props.data.price}z`} />
            )}
          </Box>
        </Item> */}
        <SmallItem>
          <IconButton
            onClick={props.addSearchBar}
            color="primary"
            aria-label="add to shopping cart"
          >
            <AddShoppingCartIcon />
          </IconButton>
        </SmallItem>
        {props.isMultipleRow ? (
          <SmallItem>
            <IconButton
              onClick={() => {
                props.removeSearchBar(props.data.id);
              }}
              color="primary"
              aria-label="remove from shopping cart"
            >
              <RemoveIcon />
            </IconButton>
          </SmallItem>
        ) : (
          <></>
        )}
      </Stack>
    </>
  );
}
